.title-all {
  text-align: center;
  font-size: 1.4rem;
  margin-bottom: 1rem;
  font-weight: bold;
}

.App {
  & > div:not(.title-all) {
    border: 1px solid black;
    padding: 12px 24px;
    &:nth-child(2) {
      /* border-bottom: none; */
    }
  }
  font-weight: normal;
  font-size: 1.1rem;
  width: 100%;
  height: 100%;
}

.title {
  transition: all .25s ease-in-out;
  & > div:first-child {
    font-weight: bold;
    font-size: 1.3rem;
    height: 54px;
  }
  & > div:not(:first-child) {
    text-indent: 1rem;
    margin-bottom: 24px;
  }
  box-sizing: border-box;
  cursor: pointer;
  &:hover {
    background-color: #d5d5d5;
  }
  &[data-selected="no"] {
    max-height: 54px;
    height: 54px;
    overflow: hidden;
  }
  max-height: 9999px;
  height: auto;
}

.small-title {
  text-decoration: underline;
}

.content {
  text-indent: 2rem;
  font-size: .8rem;
}